import ChequeService from '@/services/cheque/ChequeService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let PostDatedChequeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  data: function () {
    return {
      chequeTableData: [],
      chequeTableContainer: 'loading-container',
      chequeTableBusy: false,
      chequeTableTotalRows: 0,
      chequeTableCurrentPage: 1,
      chequeTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('CHEQUE_NUMBER'),
        field: 'chequeNumber',
        format: 'Number'
      }, {
        headerName: this.$t('ACCOUNT_NUMBER'),
        field: 'chequeNumber',
        format: 'Number'
      }, {
        headerName: this.$t('AMOUNT'),
        field: 'amount',
        format: 'Decimal'
      }, {
        headerName: this.$t('CURRENCY'),
        field: 'currencyAbbreviation'
      }, {
        headerName: this.$t('CHEQUE_DATE'),
        field: 'chequeDate',
        format: 'date'
      }, {
        headerName: this.$t('DUE_DATE'),
        field: 'dueDate',
        format: 'Date'
      }]
    }
  },
  computed: {
    chequeTableNoDataText: function () {
      return this.chequeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    chequeTableTotalPages: function () {
      return Math.ceil(this.chequeTableTotalRows / this.chequeTablePerPage)
    },
    chequeTablePagingInfo: function () {
      return this.pagingInfo(this.chequeTableCurrentPage, this.chequeTablePerPage, this.chequeTableTotalRows)
    }
  },
  methods: {
    loadChequeTableData (loadingContainerId) {
      let self = this
      this.chequeTableBusy = true
      this.chequeTableContainer = loadingContainerId || this.chequeTableContainer
      this.startLoading(this.chequeTableContainer)
      ChequeService.postDatedCheques({}).then(function (response) {
        self.stopLoading(self.chequeTableContainer)
        self.chequeTableBusy = false
        self.chequeTablePerPage = response.data.totalRows
        self.chequeTableTotalRows = response.data.totalRows
        self.chequeTableData = response.data.chequeList || []
      }).catch(function (error) {
        self.stopLoading(self.chequeTableContainer)
        self.chequeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshChequeTable () {
      this.loadChequeTableData()
    }
  },
  watch: {
    chequeTableCurrentPage: function () {
      this.refreshChequeTable()
    }
  }
}

export default PostDatedChequeTableMixin
