<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`PostDatedCheque${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import PostDatedChequeListLayout from '@/views/cheque/post-dated-cheque/post-dated-cheque-browse/PostDatedChequeListLayout'
import PostDatedChequeCardLayout from '@/views/cheque/post-dated-cheque/post-dated-cheque-browse/PostDatedChequeCardLayout'
import PostDatedChequePivotLayout from '@/views/cheque/post-dated-cheque/post-dated-cheque-browse/PostDatedChequePivotLayout'
import PostDatedChequeTableMixin from '@/mixins/cheque/PostDatedChequeTableMixin'

export default {
  name: 'post-dated-cheque-browse',
  mixins: [PostDatedChequeTableMixin],
  components: {
    PostDatedChequeListLayout,
    PostDatedChequeCardLayout,
    PostDatedChequePivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadChequeTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
