<template>
  <div>
    <nv-card class="mb-base">
      <div class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3" @click="parent.refreshChequeTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="parent.chequeTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ parent.chequeTablePagingInfo }}</span>
          </div>
        </div>
      </div>
    </nv-card>

    <nv-card class="mb-base" v-for="(cheque, index) in parent.chequeTableData" :key="index">
      <div class="mb-4 flex justify-between">
        <h3>{{ cheque.chequeNumber | placeholder('-') }}</h3>
        <div class="text-right" v-if="cheque.amount">
          <h3>{{ cheque.amount | currency('') | placeholder('-') }} {{ cheque.currencyAbbreviation }}</h3>
          <span class="text-xs">{{ $t('AMOUNT') }}</span>
        </div>
      </div>

      <div>
        <div class="nv-row mb-4">
          <div class="nv-col w-full">
            <div class="text-bold">{{ $t('ACCOUNT_NUMBER') }}</div>
            <div class="text-grey-dark">{{ cheque.accountNumber | placeholder('-') }}</div>
          </div>
        </div>
        <div class="nv-row mb-4" v-if="cheque.chequeDate">
          <div class="nv-col w-full">
            <div class="text-bold">{{ $t('CHEQUE_DATE') }}</div>
            <div class="text-grey-dark">{{ cheque.chequeDate | formatUnixDate }}</div>
          </div>
        </div>
        <div class="nv-row mb-4" v-if="cheque.dueDate">
          <div class="nv-col w-full">
            <div class="text-bold">{{ $t('DUE_DATE') }}</div>
            <div class="text-grey-dark">{{ cheque.dueDate | formatUnixDate }}</div>
          </div>
        </div>
      </div>

      <vs-divider></vs-divider>

      <div class="flex justify-between">
        <div class="flex">
          <vs-button class="p-3 mr-4" type="border" iconPack="far" icon="fa-share-alt">{{ $t('SHARE') }}</vs-button>
        </div>
        <div>
          <vs-button class="p-3 ml-4" @click="view(cheque.chequeNumber, cheque.accountNumber)" type="filled" iconPack="far" icon="fa-eye">{{ $t('FOCUS') }}</vs-button>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'

export default {
  name: 'post-dated-cheque-card-layout',
  mixins: [NumberStyleMixin],
  props: {
    parent: {
      type: Object,
      required: true
    }
  },
  methods: {
    view (id, accountNumber) {
      this.$router.push({ name: 'ViewPostDatedCheque', params: { id: id, accountNumber: accountNumber } })
    }
  }
}
</script>
