<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center flex-grow justify-between mb-base">
      <div class="flex flex-wrap-reverse items-center">
        <vs-button v-if="parent.chequeTableData.length > 0" class="p-3 mr-4" @click="exportXls" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
        <vs-button v-if="parent.chequeTableData.length > 0" class="p-3 mr-4" @click="exportCsv" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="parent.refreshChequeTable" type="filled" icon="fa-sync" icon-pack="far">{{ $t('REFRESH') }}</vs-button>
      </div>
    </div>

    <ag-grid-vue class="ag-theme-balham ag-pivot-layout"
                 :gridOptions="gridOptions"
                 :rowData="parent.chequeTableData"
                 :sideBar="sideBar"
                 :statusBar="statusBar">
    </ag-grid-vue>
  </nv-card>
</template>

<script>
import { AgGridVue } from 'ag-grid-vue'
import PivotLayoutMixin from '@/mixins/commons/PivotLayoutMixin'

export default {
  name: 'post-dated-cheque-pivot-layout',
  mixins: [PivotLayoutMixin],
  components: {
    AgGridVue
  },
  beforeMount () {
    this.gridOptions.columnDefs = [{
      headerName: this.$t('CHEQUE_NUMBER'),
      field: 'chequeNumber'
    }, {
      headerName: this.$t('REFERENCE_NUMBER'),
      field: 'auxiliary1'
    }, {
      headerName: this.$t('ACCOUNT_NUMBER'),
      field: 'accountNumber',
      enableRowGroup: true
    }, {
      headerName: this.$t('AMOUNT'),
      field: 'amount',
      type: 'numericColumn',
      valueFormatter: this.formatNumber,
      filter: 'agNumberColumnFilter'
    }, {
      headerName: this.$t('CURRENCY'),
      field: 'currencyAbbreviation',
      enableRowGroup: true
    }, {
      headerName: this.$t('DRAWEE'),
      field: 'drawee',
      enableRowGroup: true
    }, {
      headerName: this.$t('CHEQUE_DATE'),
      field: 'chequeDate',
      filter: 'agDateColumnFilter',
      enableRowGroup: true,
      valueFormatter: this.formatDate
    }, {
      headerName: this.$t('DUE_DATE'),
      field: 'dueDate',
      filter: 'agDateColumnFilter',
      enableRowGroup: true,
      valueFormatter: this.formatDate
    }]
  }
}
</script>
